import axios from "axios";
import { get } from 'lodash';
import moment from 'moment';

const fpvuLoad = async () => {
    // document.querySelector('.iframe-seed-count').src = `https://parsehub.com/api/v2/projects/te6OT0FxGtSv/last_ready_run/data?api_key=tzmeGSK1TtLY&;format=json;count=${moment().unix()}`
    try {

      const fpvu = await axios.get(`https://qucf4yuhw2.execute-api.us-east-1.amazonaws.com/dev/pvu-zone-fpvu`);
 
     const { FPVU_FUSD, FUSD_FPVU } = fpvu.data || {};
     console.log(FPVU_FUSD, FUSD_FPVU);
        if(FPVU_FUSD) {
            document.querySelector('#fpuv-fusd').innerHTML =  FPVU_FUSD || '--';
        }else {
            document.querySelector('#fpuv-fusd').parentElement.classList.add('display-none');
        }
        if(FUSD_FPVU) {
            document.querySelector('#fusd-fpvu').innerHTML =  FUSD_FPVU || '--';
        }else{
            document.querySelector('#fusd-fpvu').parentElement.classList.add('display-none');
        }
      
     
          document.querySelector('.fpvu-rate').classList.remove('display-none');
    } catch (error) {
      document.querySelector('#seed-count').innerHTML = "Something went wrong, try again later.";
      console.log('ERROR',error);
    }
  };


export default () => {
    fpvuLoad();
};