import axios from "axios";

const getStats = async (id) => {
    try {
        const res = await axios.get(`https://qucf4yuhw2.execute-api.us-east-1.amazonaws.com/dev/v2/pvu-zone-get-stat?id=${id}`)
        return res.data;
    } catch (error) {
        console.log('ERROR',error);    
        return  {};
    }

};

export const updateCheckerStat = async (id) => {
    const container = document.querySelector('.sc-stats');
    try {
        container.innerHTML = '';
        const stat = await getStats(id);
        container.innerHTML = renderStats(stat);
    } catch (error) {
        container.innerHTML = '';
        console.log('error');
    }
        
};


const renderStats = (stat) => {
  
    if (stat.damageType) {
      
      const img = {
        'Physics': 'physic.4fff40a',
        'Magic':'magic.9cad8f5',
        'HpLoss':'hp.c6760b1',
      };
      const dmg = stat.damage;//calculateDMG(plant.baseDMG, rarity, plant.dmgMultiplier);
      return `
      <div class="stat">
        <img src="https://pvu.zone/assets/${img[stat.damageType]}.svg" class="stat-icon"/>
        <span>${dmg % 1 != 0 ?  dmg.toFixed(3): dmg}</span>
        </div>
   
    `;
    } 
  return `
    <div class="stat">
      <img src="https://pvu.zone/assets/atk.svg" class="stat-icon"/>
      <span>${stat.damage}</span>
    </div>
    <div class="stat">
      <img src="https://pvu.zone/assets/def.svg" class="stat-icon"/>
      <span>${stat.defense}</span>
    </div>
    <div class="stat">
      <img src="https://pvu.zone/assets/HP.08dc7c9.svg" class="stat-icon"/>
      <span>${stat.hp}</span>
    </div>
 
  `;
  
}